import React, { useEffect, useRef, useState } from 'react'
import { useAppDispatch, useAppSelector } from '../hooks/hooks'
import { setSortItems } from '../redux/slices/filterSlice'

export const sortItems = [{ name: 'popularity', property: 'rating' },
  { name: 'price', property: 'price' },
  { name: 'A-Z', property: 'title' }]


const Sort: React.FC = () => {

  const [isVisible, setIsVisible] = useState(false)

  const sortRef = useRef<HTMLDivElement>(null)
  const dispatch = useAppDispatch()

  const sort = useAppSelector(state => state.filterSlice.sort)


  useEffect(() => {
    const handleOutsideClick = (event: MouseEvent) => {
      if (sortRef.current && !event.composedPath().includes(sortRef.current)) {
        setIsVisible(false)
      }
    }
    document.body.addEventListener('click', handleOutsideClick)
    return () => {
      document.body.removeEventListener('click', handleOutsideClick)
    }
  }, [])


  return (
    <div
      ref={sortRef}
      className='sort'>
      <div className='sort__label'>
        {
          isVisible ? (
            <svg
              width='10'
              height='6'
              viewBox='0 0 10 6'
              fill='none'
              xmlns='http://www.w3.org/2000/svg'
            >
              <path
                d='M10 5C10 5.16927 9.93815 5.31576 9.81445 5.43945C9.69075 5.56315 9.54427 5.625 9.375 5.625H0.625C0.455729 5.625 0.309245 5.56315 0.185547 5.43945C0.061849 5.31576 0 5.16927 0 5C0 4.83073 0.061849 4.68424 0.185547 4.56055L4.56055 0.185547C4.68424 0.061849 4.83073 0 5 0C5.16927 0 5.31576 0.061849 5.43945 0.185547L9.81445 4.56055C9.93815 4.68424 10 4.83073 10 5Z'
                fill='#2C2C2C'
              />
            </svg>
          ) : (
            <svg
              xmlns='http://www.w3.org/2000/svg'
              width='24'
              height='24'
              viewBox='0 0 24 24'
            >
              <path fill='none' d='M0 0h24v24H0V0z' />
              <path d='M7 10l5 5 5-5H7z' />
            </svg>
          )

        }

        <b>Sorting by:</b>
        <span onClick={() => setIsVisible(!isVisible)}>{sort.name}</span>
      </div>
      {
        isVisible &&
        <div className='sort__popup'>
          <ul>
            {sortItems.map(((obj, i) => (
              <li key={i}
                  onClick={() => {
                    dispatch(setSortItems(obj))
                    setIsVisible(!isVisible)
                  }}
                  className={sort.name === obj.name ? 'active' : ''}>
                {obj.name}
              </li>
            )))}
          </ul>
        </div>
      }

    </div>
  )
}

export default Sort